.header {
  color: $black-text;
  padding: 40px 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  background: #fff;
}
.active {
    background: rgba(#fff, .9);
    box-shadow: 0 1px 10px rgba(0, 0, 0, 0.15);
    padding: 30px;
  }

.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;

}

.logo {
  display: flex;
  align-items: center;
}

.bars {
  font-size: 1.6rem;
  &:visited {
    color: $black-text;
  }
}