.slider {
  display: flex;
  align-items: center;
  &__inner {
    display: flex;
    width: 48%;
    justify-content: space-between;
    margin: 0 auto;
    align-items: center;
    .slick-current {
      .slider__user {
        width: 130px;
        height: 130px;
        opacity: 1;
        box-shadow: 10px 30px 16px rgba($shadow-color, .15);
      }
    }
  }
    &:last-child {
      margin-right: 0;
    }
}

.comments__inner {
  position: relative;
}

.slider__user {
  border-radius: 50%;
  width: 90px;
  height: 90px;
  opacity: 0.3;
}

.slider__arrow {
  cursor: pointer;
  font-size: 2.8rem;

  &:hover {
    color: $blue;
  }
}

.slider__comment{
  display: flex;
  justify-content: center;
}

@media (max-width: 992px) {
  .slider {
    &__inner {
      width: 80%;
    }
  }
}

