.intro {
  padding: 210px 0 470px;
  position: relative;
  overflow: hidden;
  background: url(../image/banner_background.jpg) center/cover no-repeat;
}

.intro__inner {
  text-align: center;
}

.intro__title {
  max-width: 785px;
  width: 100%;
  margin: 0 auto;
  .intro-text {
  margin: 40px 0 60px;
}
}

.intro__btn {
 display: flex;
 justify-content: center;
 margin-bottom: 60px;
}

.intro__img {
  position: absolute;
  bottom: -300px;
  left: 50%;
  margin-left: -185px;
  z-index: 0;
}

.title {
  @include main-title($black-text);
}

