.nav {
  font-size: 1.6rem;
  display: flex;
  align-items: center;
  &--left {
    .nav__item {
      margin-left: 38px;
      &:first-child {
      margin-left: 44px;
      }
    }   
  }
  &--right {
    text-align: right;
    .nav__item {
      margin-left: 30px;
      &:first-child {
        margin-left: 0;
      }
    }
  }
}

.nav__list {
  display: flex;
  align-items: center;
}

.nav__link {
  color: $black-text;
  font-size: 1.6rem;
  font-weight: 700;
  &--help {
    color: $gray-text;
  }
  &:hover {
    color: $blue;
  }
}

.bars {
  position: absolute;
  &:hover {
    color: $blue;
  }
}

