.slide-out-menu {
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 365px;
  max-width: 650px;
  height: 100%;
  left: 0;
  top: 0;
  transform: translateX(-100%);
  padding: 40px;
  z-index: 50;
  background: #26272D;
  transition: transform .7s cubic-bezier(0.545, 0, .05, 1);
}

.menu-active {
  transform: translateX(0%);
}

.menu-close {
  position: absolute;
  right: 20px;
  top: 52px;
  color: #fff;
  font-size: 18px;
  &:hover {
    color: $blue;
  }
}

.menu-logo {
  margin-top: 20px;
  margin-bottom: 40px;
}

.slide-out-menu {
  &__list {
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    margin-bottom: 60px;
  }
   &__item {
      margin-bottom: 7px;
      a {
        color: #fff;
        font-size: 1.6rem;
        &:visited {
         color: #fff;
        }
        &:hover:not(.btn-menu) {
          color: $blue;
        }
      }
  }
}

.btn-menu {
  margin-top: 20px;
  width: 131px;
}

.slide-out-menu__footer {
  .more-info {
    color: #fff;
    margin-bottom: 25px;
    a {
      margin-left: 10px;
      color: $blue;
    }
  }
}

.socials {
  width: 230px;
  display: flex;
  justify-content: space-between;
  i {
    color: #fff;
    &:hover {
      color: $blue;
    }
  }
}