.get {
  padding: 50px 0 100px;
}

.get__title, .subscribe__title {
  text-align: center;
  margin-bottom: 45px;
}

.suptitle {
  font-size: 1.2rem;
  line-height: 2.2rem;
  text-transform: uppercase;
  margin-bottom: 30px;
}

.form-title {
  @include title-h2($special-title);
}

.form {
  margin-bottom: 50px;
}

.get-form, .subscribe-form {
  position: relative;
  margin: 0 auto;
}

.get-form {
  width: 500px;
}

.subscribe-form {
  width: 570px;
}

.input {
  width: 100%;
  padding: 15px 25px;
  border-radius: 25px;
  border: 1px solid #CCCCCF;
  font-family: inherit;
  font-size: 1.6rem;
}

.get__btn, .subscribe__btn {
  position: absolute;
  right: 4px;
  top: 3px;
  transform: translateY(1px);
}

.get__btn {
  padding-right: 12px;
}

.subtitle {
  color: rgba($special-title, .3);
  margin-bottom: 20px;
}

.app__btn {
  display: flex;
  justify-content: center;
  max-width: 210px;
  margin: 0 auto;
  padding: 4px 23px 14px 17px;
  font-size: 2.3rem;
  line-height: 2rem;
  color: #fff;
  background: #131315;
  border-radius: 30px;
  i {
    transform: translateY(26%);
    font-size: 2.6rem;
    margin-right: 17px;
  }
  span {
    font-size: 1.2rem;
    line-height: 1.2rem;
  }
}