.map {
  padding: 210px 0 150px;
  position: relative;
  overflow: hidden;
}

.map__inner {
  display: flex;
}

.map__content {
  flex-basis: 470px;
}

.map__text {
  margin: 70px 0;
}

.map__partners-title {
  font-size: 1.2rem;
  line-height: 2.2rem;
  font-weight: 700;
  color: $black-text;
  text-transform: uppercase;
  margin-bottom: 30px;
}

.partners__wrap {
  img {
    margin-right: 30px;
    &:last-child{
      margin-right: 0;
    }
  }
}

.map__img {
  position: absolute;
  left: 50%;
  top: 23%;
}