html {
  font-size: 10px;
}

body {
  font-family: 'Lato', sans-serif;
  font-size: 1.6rem;
  line-height: 2.8rem;
  color: $gray-text;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
}

button {
  border: none;
  padding: 0;
  margin: 0;
  border-style: none;
}

a {
  display: inline-block;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

.text-subtitle {
  @include text-subtitle();
   p {
    margin-bottom: 40px;
    &:last-child {
      margin-bottom: 0;
    }
  }
}

.text-center {
  text-align: center;
}

.title-h2 {
  @include title-h2($black-text);
}