
@media (max-width: 1199px) {
  .container {
    max-width: 1140px;
  }
} 

@media (max-width: 1160px) {
  .container {
    max-width: 970px;
  }
} 

@media (max-width: 991px) {
  .container {
    max-width: 750px;
  }
} 

@media (max-width: 767px) {
  .container {
    max-width: 720px;
  }
} 

@media (max-width: 575px) {
  .container {
    max-width: 540px;
  }
} 

@media (max-width: 1110px) {
  .features {
    padding-bottom: 120px;
    &__item {
      padding: 20px;
    }
  }
}

@media only screen and (max-width: 992px) {

  .nav--left, .nav--right {
    display: none;
  }

  .header {
    position: relative;
    .logo {
      position: absolute;
      left: 50%;
      top: 40px;
      transform: translateX(-50%);
    } 
  }

  .intro {
    padding: 150px 0 370px;
    &__img {
      bottom: -390px;
    }
  }

  .slide-out-menu {
    width: 300px;
  }

  .features {
    padding: 100px 0;
    &__inner {
      flex-direction: column;
    }
    &__item {
      padding: 0;
      flex-basis: auto;
      &:first-child {
        margin-bottom: 80px;
      }
    }
    &__title {
      margin-bottom: 30px;
    }
  }

  .protect {
    padding: 80px 0 60px;
    display: flex;
    flex-direction: column-reverse;

    &__content {
      flex-basis: auto;
    }
  
    &__img {
      position: relative;
      bottom: auto;
      right: auto;
      padding: 0 15px;
      max-width: 700px;
      width: 100%;
      margin: 64px auto;
      margin-top: 64px;
    }
  }

  .map {
    padding: 80px 0 100px;
    &__content {
      flex-basis: auto;
    }
      &__img {
      position: relative;
      bottom: auto;
      left: auto;
      padding: 0 15px;
      width: 100%;
      max-width: 700px;
      margin: 64px auto;
      margin-top: 64px;
    }
  } 

  .interface {
    padding: 120px 0 620px;

    .img-list {
      li {
        position: absolute;
        top: 68px;
        img {
          width: 100%;
        }
        &:nth-child(1) {
          margin-left: -670px;
        }
        &:nth-child(2) {
          margin-left: -400px;
        }
        &:nth-child(5) {
          margin-right: -670px;
        }
        &:nth-child(4) {
          margin-right: -400px;
        }
        &:not(.main-img) {
          width: 240px;
        }
        
      }
      .main-img {
          width: 292px;
          margin: 0 auto;
          transform: translate3d(10px, -40px, 0);
        }
    }
  }

  .comments {
    margin: 140px 0 200px;
    &__slider {
      margin-bottom: 60px;
    }
    .slider__arrow {
      display: none;
    }
  }

  .footer {
    padding: 80px 0 0;
    &__top {
      flex-direction: column;
    }
    &__left {
      width: 100%;
      margin-bottom: 60px;
    }
  }
}

@media only screen and (max-width: 700px) {
  .intro {
    padding-top: 100px;
  }
  .footer {
    &__right-col {
      &:last-child {
        margin-right: 0;
      }
      
    }
  }
}

@media only screen and (max-width: 620px) {
  html {
    font-size: 9px;
  }
  .protect {
    &__img {
      width: 500px;
    }
  }
  .interface {
    padding: 100px 0 550px;

    .img-list {
      li {
        position: absolute;
        top: 68px;
        img {
          width: 100%;
        }
        &:nth-child(1) {
         display: none;
        }
        &:nth-child(2) {
          margin-left: -320px;
        }
        &:nth-child(5) {
          display: none;
        }
        &:nth-child(4) {
          margin-right: -320px;
        }
        &:not(.main-img) {
          width: 200px;
        }
        
      }
      .main-img {
          width: 242px;
          margin: 0 auto;
          transform: translate3d(10px, -20px, 0);
        }
    }
  }

  .get {
    &-form {
      width: 100%;
    }
    .input {
      margin-bottom: 30px;
      padding: 12px 20px;
    }
    &__btn {
      position: static;
      margin: 0 auto;
    }
  }
  .subscribe {
    padding: 80px 0;
    &-form {
      width: 100%;
    }
    .input {
      margin-bottom: 30px;
      padding: 12px 20px;
    }
    &__btn {
      position: static;
      margin: 0 auto;
    }
  }
  .footer {
    &__inner {
      flex-direction: column;
    }
    &__link-web {
      margin-bottom: 30px;
    }
    }
  }


@media only screen and (max-width: 520px) {
  html {
    font-size: 8px;
  } 
  
  .protect {
    &__img {
      width: 400px;
    }
  }
  .comments {
    .slider {
      &__user {
        width: 70px;
        height: 70px;
      }
    }
  }

  .plan {
    padding-top: 0;
  }
  .footer {
    &__right-col {
      margin-bottom: 20px;
    }
  }
}

@media only screen and (max-width: 408px) {
  .protect {
    &__img {
      width: 300px;
    }
  }
  .comments {
    .slider {
      &__user {
        width: 50px;
        height: 50px;
      }
    }
  }
}

@media only screen and (max-width: 355px) {
  .protect {
    &__btn {
      .btn {
        margin-bottom: 20px;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
}