.interface {
  padding: 100px 0 880px;
  overflow: hidden;
  position: relative;
  background: #f4f4f5;
}

.interface__title {
  text-align: center;
  margin-bottom: 50px;
  &-main {
    margin-bottom: 40px;
    position: relative;
    padding-bottom: 50px;
    &:after {
      content: '';
      position: absolute;
      display: block;
      bottom: 0;
      right: 50%;
      transform: translateX(50%);
      z-index: 1;
      height: 2px;
      width: 50px;
      background: $blue;
    }
  }
}

.subtitle {
  text-transform: uppercase;
  font-size: 1.2rem;
  line-height: 2.2rem;
  font-weight: 700;
}

.img-list {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  position: relative;
  li {
    position: absolute;
    top: 90px;
    img {
      max-width: 100%;
    }
    &:nth-child(1) {
      left: 50%;
      margin-left: -880px;
    }
    &:nth-child(2) {
      left: 50%;
      margin-left: -530px;
    }
    &:nth-child(5) {
      right: 50%;
      margin-right: -880px;
    }
    &:nth-child(4) {
      right: 50%;
      margin-right: -530px;
    }
    &:not(.main-img) {
      width: 320px;
      box-shadow: 10px 30px 16px rgba($shadow-color, .15), 20px 40px 66px rgba($shadow-color2, .28);
    }
    
  }
  .main-img {
    top: -20px;
    transform: translateX(15px);
  }
}