.plan {
  padding: 100px 0 150px;
  background: #f4f4f5;
}

.plan__inner {
  display: flex;
  justify-content: space-between;
}

.plan__select {
  width: 350px;
}

.plan__title {
  margin-bottom: 60px;
}

.plan__determinate {
  font-size: 1.2rem;
  line-height: 2rem;
  margin-bottom: 20px;
  text-transform: uppercase;
  color: $black-text;
  font-weight: 700;
}

.toggle-container {
  margin-bottom: 70px;
}

.switch-toggles {
  position: relative;
  display: flex;
  align-items: center;  
  height: 48px;
  width: 263px;
  border-radius: 25px;
  background: #eeeeee;
  border: 1px solid #D4D5D6;
}

.individual, .company {
  position: relative;
  width: 130px;
  padding: 13px 10px;
  z-index: 2;
  text-align: center;
  cursor: pointer;
  font-size: 16px;
  transition: color 0.7s cubic-bezier(0.545, 0, 0.05, 1);
} 

.individual.active-plan, .company.active-plan {
  color: $blue;
} 

.switch-toggles:after {
  content: '';
  position: absolute;
  width: 124px;
  height: 42px;
  left: 2px;
  top: 2px;
  background: #fff;
  border-radius: 30px;
  box-shadow: 2.5px 4.33px 20px 0px rgba(61, 61, 62, 0.059);
  transition: all 0.7s cubic-bezier(0.545, 0, 0.05, 1);
}

.switch-toggles.active-plan:after {
  left: 135px;
  top: 2px;
} 

.plan__text {
  font-size: 2rem;
  line-height: 2.8rem;
  margin-bottom: 40px;
}

.plan__team, .plan__link {
  font-size: 1.4rem;
  line-height: 2.4rem;
}


.choose__individual, .choose__company {
  display: flex;
  align-items: center;
}

.card {
  width: 340px;
  padding: 60px 50px;
  text-align: center;
  border-radius: 10px;
  &--starter {
    box-shadow: 10px 15px 50px rgba(#5c5f65, .2);
    background: #ffffff;
    height: 550px;
  }
  &--pro {
    background: #EBEBED;
    padding: 40px 50px;
    height: 510px;
    border-top-left-radius: 0px;
    border-bottom-left-radius: 0px;
  }
}

.card__title {
  font-size: 3.2rem;
  line-height: 3.8rem;
  font-weight: 300;
  color: $black-text;
  margin-bottom: 30px;
}

.card__price {
  display: inline-block;
  font-size: 1.1rem;
  line-height: 2.2rem;
  color: $black-text;
  font-weight: 700;
  text-transform: uppercase;
  padding: 3px 17px;
  border: 1px solid #D4D4D5;
  border-radius: 15px;
  margin-bottom: 35px;
}

.card__text {
  font-size: 2rem;
  line-height: 2.8rem;
  width: 180px;
  margin: 0 auto 40px;
}

.card__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 50px;
  padding: 0;
  &--start {
    margin-bottom: 80px;
  }
}

.card__item {
  color: $black-text;
  position: relative;
  padding-left: 25px;
  &:before {
    content: '';
    position: absolute;
    top: 10px;
    left: 0;
    width: 15px;
    height: 10px;
    background: url(../../../build/image/svg/check.png) center left no-repeat;
  }
}


.plan {
  @media (max-width: 1160px) {
    .plan__select {
      width: 290px;
    }
    .card {
      width: 300px;
      padding: 40px 30px;
      &--pro {
        padding: 20px 30px;
    }
  }
}
@media (max-width: 992px) {
  padding: 120px 0 150px;
    .plan__inner {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
  }
  .plan__select {
    width: 100%;
    margin-bottom: 70px;
  }
  .card {
  width: 50%;
  }
}
@media (max-width: 615px) {
 .choose__individual, .choose__company {
  display: flex;
  align-items: center;
  flex-direction: column;
} 
.card {
  width: 340px;
  padding: 60px 50px;
  &--pro {
    width: 310px;
    padding: 40px 50px;
}
}
}
@media (max-width: 415px) {
  .card {
  width: 300px;
  padding: 50px 40px;
  &--pro {
    width: 280px;
    padding: 30px 40px;
}
}
}
}
