.btn {
  font-size: 1.6rem;
  display: flex;
  align-items: center;
  font-weight: 500;
  border-radius: 21px; 
  i {
    font-size: 1.8rem;
    margin-right: 15px;
  }
}

.btn--blue, .btn--hack {
  color: #fff;
  padding: 7px 22px;
  background: $blue;
  i {
     transform: translateY(-2px);
  }
  &:visited {
    color: #fff;
  }
}

.btn--main {
  color: $black-text;
  padding: 12px 45px 12px 42px;
  border-radius: 25px;
  border: 1px solid #DADCDE;
  background: transparent;
  cursor: pointer;
  i {
    font-size: 1.4rem;
  }
}

.btn--hack {
  max-width: 210px;
  margin-right: 20px;
  margin-bottom: 0px;
  padding: 7px 20px;
  i {
    font-size: 1.6rem;
    transform: translateY(1px);
    margin-left: 40px;
  }
}

.btn--more {
  color: $black-text !important;
  border: 1px solid #E9E9EA;
  background: #fff;
  padding: 7px 31px;
}

.btn--form {
  background: $green;
  color: #ffffff;
  padding: 12px 30px;
  i {
    margin-left: 18px;
  }
}

.card__btn {
width: 200px;
 padding: 15px 40px;
  border: 1px solid $blue;
  border-radius: 25px;
  color: $blue;
  font-size: 16px;
  &--active {
    color: #fff;
    background: $blue;
  }
}