.footer {
  padding: 100px 0 0;
}

.footer__top {
  display: flex;
  justify-content: space-between;
  margin-bottom: 90px;
}

.footer__left {
  width: 270px;
  .logo {
    margin-bottom: 30px;
  }
}

.footer__text {
  font-size: 1.4rem;
  line-height: 2.4rem;
  margin-bottom: 24px;
}

.app-store {
  margin-bottom: 30px;
}

.footer__media {
  max-width: 224px;
  display: flex;
  justify-content: space-between;
}

.media-link {
  color:$gray-text;
  font-size: 1.8rem;
  &:visited {
    color: inherit;
  }
  &:hover {
    color: $blue;
  }
}

.footer__right {
  display: flex;
  flex-wrap: wrap;
  &-title {
    font-size: 1.2rem;
    line-height: 2.2rem;
    color: $black-text;
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 25px;
  }
  &-list {
    display: flex;
    flex-direction: column;
  }
  &-item {
    font-size: 1.6rem;
    line-height: 3.6rem;
    a {
      color: $gray-text;
      &:visited {
        color: inherit;
      }
      &:hover {
        color: $blue;
      }
    }
  }
}

.footer__right-col {
  margin-right: 100px;
}

.footer__bottom {
  background: #EDEDEE;
  padding-top: 30px;
}

.footer__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 0;
  border-top: 1px solid #E4E4E5;
}

.footer__link-web {
  font-size: 1.4rem;
  line-height: 2.4rem;
  a {
    color: $blue;
  }
}

.footer__author {
  display: flex;
  align-items: center;
  font-size: 0.9rem;
  line-height: 2.2rem;
  font-weight: 700;
  color: $black-text;
  text-transform: uppercase;
  img {
    margin-left: 10px;
    transform: translateY(-3px);
  }
}
