.features {
  padding: 120px 0 240px;
}

.features__inner {
  display: flex;
  justify-content: space-between;
  padding: 0 25px;
}

.features__item {
  flex-basis: 500px;
  img {
    margin-bottom: 40px;
  }
}

.features__title {
  @include title-h3();
  margin-bottom: 40px;
}

.features__text {
  font-size: 1.8rem;
  line-height: 2.6rem;
  p {
    margin-bottom: 28px;
    &:last-child {
      margin-bottom: 0;
    }
  }
}