// Colors

$gray-text: #8c8f94;
$black-text: #26292c;
$blue: #267DF4;
$green: #6FC754;
$black-btn: #131315;
$link: #3b55e6;
$special-title: #3a3d4b;
$shadow-color: #3d3d3e;
$shadow-color2: #5c5f65;
$gold: #F2BC3B;

//Mixins

@mixin main-title($color) {
  font-size: 6.2rem;
  line-height: 7.2rem;
  color: $color;
  text-align: center;
  font-family: 'Varela Round', 'Lato' sans-serif;
}

@mixin title-h2($color) {
  font-size: 4.2rem;
  line-height: 5.2rem;
  color: $color;
  font-family: 'Varela Round', sans-serif;
}

@mixin title-h3() {
  font-size: 2.6rem;
  line-height: 3.4rem;
  color: $black-text;
  font-weight: 300;
}

@mixin text-subtitle {
  font-size: 2rem;
  line-height: 2.8rem;
  color: $gray-text;
}

//Media

$xs: (max-width: 575px);
$sm: (max-width: 767px);
$md: (max-width: 991px);
$lg: (max-width: 1199px);