.protect {
  position: relative;
  padding: 150px 0 230px;
}

.protect__img {
  position: absolute;
  right: 56%;
  bottom: 10px;
}

.protect__inner {
  display: flex;
  flex-direction: row-reverse;
}

.protect__content {
  flex-basis: 470px;
}

.protect__text {
  margin: 70px 0 80px;
}

.protect__btn {
  display: flex;
  flex-wrap: wrap;
  .btn {
    margin-bottom: 0;
  }
 
  i {
    transform: translateX(0);
    transition: all .2s linear;
  }
  &:hover {
  i {
    transform: translateX(10px);
  }
  }
}