.comments {
  margin: 120px 0 180px;
}

.comments__slider {
  margin-bottom: 130px;
}

.comments__description {
  max-width: 570px;
  margin: 0 auto;
  text-align: center;
}

.comments__title {
  color: $black-text;
  font-size: 3.2rem;
  line-height: 3.8rem;
  font-weight: 300;
  margin-bottom: 50px;
}

.comments__text {
  font-size: 2rem;
  line-height: 2.8rem;
  margin-bottom: 30px;
}

.comments__rate {
  color: $gold;
  margin-bottom: 50px;
  i {
    margin-right: 10px;
    &:last-child {
    margin-right: 0;
  }
  }
}

.comments__name {
  text-transform: uppercase;
  color: $black-text;
  font-size: 1.2rem;
  line-height: 2.2rem;
  font-weight: 700;
}